import React from 'react';
import { NavLink } from 'react-router-dom';
import logo from './logo.png';
import { Navbar } from 'react-bootstrap';

const Navigation = () => {
    return (
        <Navbar bg="light" expand="md" sticky="top" collapseOnSelect className='border-bottom' >
            <div className="container">
                <Navbar.Brand href='/'><img className='nav-logo' src={logo} alt="" /> BartekShop</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav" className='justify-content-end'>
                    <ul className='navbar-nav navbar-right'>
                        <li className="nav-item"><NavLink to="/services" className='nav-link h5'>Oferta</NavLink></li>
                        <li className="nav-item"><NavLink to="/about" className='nav-link h5'>Informacje</NavLink></li>
                        <li className="nav-item"><NavLink to="/contact" className='nav-link h5'>Kontakt</NavLink></li>
                    </ul>
                </Navbar.Collapse>
            </div>
        </Navbar >
    );
}

export default Navigation;