const OneItem = (props) => {
    return (
        <div key={props.item.id} className="col-md-4">
            <div className="card bg-light text-white mb-2">
                <img src={props.item.img} className='card-img-top' alt="" />
                <div className="card-body">
                    <h5 className="card-title text-dark mb-0">{props.item.name}
                        {props.item.badge ? <span className={props.item.badgeTyp}>{props.item.badge}</span> : null}
                    </h5>
                    <p className="card-text text-muted mt-0">{props.item.type}</p>
                    <NavLink to="#" className="btn btn-success">Kup za {props.item.price} PLN</NavLink>
                </div>
            </div>
        </div >
    )
}
