import React from 'react';

const Footer = () => {
    return (<>
        <footer className='bg-dark'>
            <div className="bg-secondary mt-5">
                <div className="container">
                    <div className="row">
                        <div className="col-7 pt-3">
                            <h4 className="h5 text-light ml-4">BartekShop</h4>
                            <p className='text-light'>Podaj nam swój adres email, jeśli lubisz otrzymywać niechcianą korespondencję</p>
                        </div>
                        <div className="col-5 py-3">
                            <h4 className="h5 text-light ml-4">Newsletter</h4>
                            <div className="input-group">
                                <input className='p-1' type="email" placeholder='Twój adres @' />
                                <div className="input-group-append">
                                    <button onClick={(e) => {
                                        e.preventDefault();
                                        return alert('Nie żartuj ;D')
                                    }} className="btn btn-info">Zapisz się!</button></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <p className='text-muted py-4 m-0 text-center'>copyright &copy; BartekShop</p>
            </div>
        </footer>

    </>);
}

export default Footer;