import React from 'react';
import { Route, Switch } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

const Jumbotron = (props) => {
    const createClass = `jumbotron-bg bg-${props.site}`;
    return (
        <>
            <div className="jumbotron">
                <div className="container">
                    <div className={createClass}></div>
                    <h1 className="display-4 text-white pull-right">Witaj u Bartka</h1>
                    <p className="lead text-white">Smacznie i zdrowo.</p>
                </div>
            </div>
            <div className="container">
                <nav aria-label="breadcrumb">
                    <ol className="breadcrumb">
                        <li className="breadcrumb-item active" ><em>Strona główna</em></li>
                        <li className="breadcrumb-item " ><em>Produkty</em></li>
                    </ol>
                </nav>
            </div>
        </>)
}

const Header = () => {
    return (
        <>
            <Switch>
                <Route path='/' exact render={(props) => <Jumbotron site={'home'} {...props} />} />
                <Route path='/about' exact render={(props) => <Jumbotron site={'about'} {...props} />} />
                <Route path='/services' exact render={(props) => <Jumbotron site={'services'} {...props} />} />
                <Route path='/contact' exact render={(props) => <Jumbotron site={'contact'} {...props} />} />
                <Route render={(props) => <Jumbotron site={'home'} {...props} />} />
            </Switch>
        </>
    );
}

export default Header;